.--slider .fa-check {
  color: #FFFFFF;
  position: absolute;
  left: 15px;
  font-size: 16px;
  display: none;
}

.--slider .fa-times {
  color: #FFFFFF;
  position: absolute;
  font-size: 16px;
  right: 15px;
}

.--slider {
  position: absolute;
  cursor: pointer;
  display: flex;
  align-items: center;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #db3a34;
  transition: .4s;
  border-radius: 50px;
}

.--slider:before {
  position: absolute;
  content: "";
  height: 27px;
  width: 27px;
  left: 2px;
  border-radius: 50%;
  background-color: white;
  transition: .2s;
}

.--switch input:checked+.--slider .fa-check {
  display: block;
}

.--switch input:checked+.--slider .fa-times {
  display: none;
}

.--switch input:checked+.--slider {
  background-color: #52b69a;
}

.--switch input:focus+.--slider {
  box-shadow: 0 0 1px #52b69a;
}

.--switch input:checked+.--slider:before {

  -webkit-transform: translateX(25px);
  transform: translateX(25px);
}

