.list__table {
    position: relative;
    overflow: visible;
    flex: 0 0 auto;
    border-right: 1px solid #e6e6e6;
    border-bottom: 1px solid #e6e6e6;
    border-left: 1px solid #e6e6e6;
    border-radius: 4px;
  }
  
  .list__table-sort {
    margin-right: -1px;
    margin-left: -1px;
    padding-right: 21px;
    padding-left: 21px;
    align-items: center;
    border: 1px solid #e6e6e6;
    border-top-left-radius: 4px;
    border-top-right-radius: 4px;
  }
  
  .btn.btn__sort {
    color: #3a94d4;
    padding-top: 14px;
    padding-bottom: 14px;
  }
  
  .list__img-badge {
    position: absolute;
    left: auto;
    top: 0;
    right: 0;
    bottom: auto;
    display: -webkit-box;
    display: -webkit-flex;
    display: -ms-flexbox;
    display: flex;
    width: 21px;
    height: 21px;
    margin-top: -5px;
    margin-right: -5px;
    padding-top: 3px;
    -webkit-box-pack: center;
    -webkit-justify-content: center;
    -ms-flex-pack: center;
    justify-content: center;
    -webkit-box-align: center;
    -webkit-align-items: center;
    -ms-flex-align: center;
    align-items: center;
    border-radius: 50%;
    background-color: #0e75f9;
    color: #fff;
    font-size: 11px;
    font-weight: 700;
  }
  
  .qty__tag {
    background-color: $gray-100;
  }
  
  .category__link {
    .category__img-wrap {
      margin: 6px 9px 6px 6px;
      padding: 9px 0;
      flex: 0 0 auto;
      height: 34px;
      max-height: 34px;
      max-width: 34px;
      min-height: 34px;
      min-width: 34px;
    }
  }
  
  .billing__buttons {
    gap: 8px;
  }
  
  .progress__wrap {
    max-width: 300px;
  
    @include media-breakpoint-down(md) {
      width: 200px;
    }
  }
  
  .table__list-item + .table__list-item {
    //border-top: 1px solid #e6e6e6;
  }
  
  .billing__name {
    width: 150px;
  
    @include media-breakpoint-down(sm) {
      width: auto;
    }
  }
  
  @include media-breakpoint-down(sm) {
    .post__value {
      order: 1;
    }

    .qty__tag {
      order: 1;
    }
  }